const tasks = (state = defaultState, action: any) => {
  switch (action.types) {
    case tasksTypes.UPDATE_LIST_TASKS:
      return Object.assign({}, state, action.tasks);
    // case tasksTypes.ADD_TASK:
    //   return ;
    default:
      break;
  }
  return state;
}

export default tasks;
