import React, { Component, Fragment } from 'react';
import Dropdown from '../Dropdown/Dropdown';
import './WrapDropdown.css';

class WrapDropdown extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      show: false,
    }
  }

  render() {
    const { color, extClass, btnName } = this.props;
    return (
      <div className='wrap-dropdown'>
        <button className={`btn btn-sm bs-1 btn-${color} ${extClass}`} onClick={this.toggleDropdownSort}>{btnName}</button>
        <Dropdown list={this.props.list} show={this.state.show} handleClick={this.handleClick}/>
      </div>
    )
  }

  handleClick = (event: any) => {
    this.props.handleClick(event);
    this.setState({ show: false });
  };

  toggleDropdownSort = () => {
    this.setState({ show: !this.state.show });
  }
}

export default WrapDropdown;
