import * as loadingTypes from '../../const/loading';

const defaultState = {
  status: false
};

const loading = (state = defaultState, action: any) => {
  switch (action.type) {
    case loadingTypes.ON_LOADING:
      state = Object.assign({}, {...state}, { status: true });
      return state;
    case loadingTypes.OFF_LOADING:
      state = Object.assign({}, {...state}, { status: false });
      return state;
    default:
      return state;
  }
}

export default loading;
