import * as mainContainerMTTypes from '../../const/mainContainerMTTypes';

const defaultState = {
  marginTop: 0
};

const mainContainerMT = (state = defaultState, action: any) => {
  switch (action.type) {
    case mainContainerMTTypes.UPDATE_MAIN_CONTAINER_MT:
      return Object.assign({}, {...state}, action.mTObj);
    default:
      return state;
  }
}

export default mainContainerMT;
