import React, { Component } from 'react';
import { connect } from 'react-redux';
import Page from '../../component/Layout/Page';
import { tasksRef } from './../../firebase/firebase';
import { actUpdateListTasks } from './../../actions/tasks';
import './Tasks.scss';
import TaskService from './../../services/Tasks';
import Task from './../../models/Task';
import Loading from './../../component/Loading/Loading';
import InProcess from './../../component/InProcess/InProcess';
import { actTurnOnLoading, actTurnOffLoading } from './../../actions/loading';
// import BoxFixed from './../../component/boxFixed/boxFixed';
import * as Resources from './../../resources/index';

// const Items = (items:[] = []) => {
//   return 
// }

class Tasks extends Component<any, any> {
  public refs: {
    textInput: HTMLInputElement;
  }
  // private pwdRef: React.RefObject<HTMLInputElement>;
  // private isDoneRef: React.RefObject<HTMLInputElement>;

  constructor(props: any) {
    super(props);
    // this.pwdRef = React.createRef();
    // this.isDoneRef = React.createRef();

    this.state = {
      text: '',
      isAdd: false,
      isPostingTask: false,
      isDone: false,
      isEditing: false,
      isUpdating: false,
      isSaving: false,
      isDeleting: false,
      itemEditing: null,
      itemDeleteId: -1,
      updatingItemId: -1,
      term: '',
      todos: [],
    };
  }

  componentWillMount() {
    this.loadList();
  }

  componentDidMount() {
    // tasksRef.on('value', (snapshot) => {
    //   this.props.dispatchUpdateListTasks(snapshot.val());
    //   console.log(snapshot.val());
    // }, (error: any) => {
    //   console.log(error);
    // });
  }

  componentWillReceiveProps(nextProps: any) {
    // console.log(nextProps);
  }

  handleChange = (event: any) => {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
    this.setState({
      [name]: value
    });
  }

  updateTodo = (todo: Task, event: any) => {
    const updateTodo = Object.assign({}, {...todo}, { isDone: event.target.checked});
    this.setState({
      isUpdating: true,
      updatingItemId: todo._id
    });
    
    TaskService.updateTask(updateTodo)
    .then((res: any) => {
      this.setState({
        todos: res.data,
        isUpdating: false,
        updatingItemId: -1
      });
    })
    .catch((error: any) => {
      console.log(error);
      this.setState({ updatingItemId: -1 });
    });
  }

  handleDeleteTask = (id: String) => {
    this.setState({
      isDeleting: true,
      itemDeleteId: id
    });
    if (id === '' || id === undefined) {
      console.log('Remove nothing id', id);
      return;
    } else {
      this.setState({ itemDeleteId: id });
      TaskService.destroyTask(id)
      .then((res: any) => {
        if (res.status === 200) {
          console.log(`Delete item ${id} completed!`);
          this.setState({
            todos: res.data,
            isDeleting: false,
            itemDeleteId: -1
          });
        } else {
          console.log(`Delete item ${id} uncompleted!`);
          this.setState({ itemDeleteId: -1, isDeleting: false });
        }
      })
      .catch((error: any) => {
        console.log('Error: ', error);
        this.setState({ itemDeleteId: -1, isDeleting: false });
      });
    }
  }

  handleSaveTask = () => {
    this.setState({
      isSaving: true,
    });
    const { itemEditing, text } = this.state;
    if (itemEditing.text !== '' && text !== '') {
      const tmp = Object.assign({}, { ...itemEditing }, {text: text});
      TaskService.updateTask(tmp)
      .then((res: any) => {
        if (res.status === 200) {
          this.setState({
            todos: res.data,
            isSaving: false,
            isEditing: false,
            itemEditing: null,
            text: '',
          });
        }
      })
    } else {
      console.log('update faile! text can not empty');
      this.setState({
        isSaving: false,
        isEditing: false,
        itemEditing: null,
        text: '',
      });
    }
  }

  handleEditTask = (item: any) => {
    this.setState({ isEditing: true, itemEditing: item, text: item.text });
  }
  
  handleCancelTask = () => {
    this.setState({ isEditing: false, itemEditing: null, text: '' });
  }

  handleSubmit = (event: any): void => {
    event.preventDefault();
    this.setState({ isPostingTask: true });

    const task = { 
      isDone: this.state.isDone, 
      text: this.state.text
    };
    
    TaskService.addTask(task)
    .then((res: any) => {
      if (res.status === 200) {
        // add success
        this.setState({
          todos: res.data,
          isAdd: false,
          isPostingTask: false,
          text: '',
          isDone: false
        });
      }
    })
    .catch((err: any) => {
      // add error
      this.setState({
        isAdd: false,
        isPostingTask: false,
        text: '',
        isDone: false
      });
    });

    // if(this.textRef.current.value === '') {
    //   this.textRef.current.focus();
    // } else {
    //   tasksRef.push({
    //     name: this.textRef.current.value,
    //     email: 'tot@mail.com'
    //   }); 
    // }
  }

  handleAddTask = () => {
    this.setState({ isAdd: true });
  }

  handleCloseFormAddTask = () => {
    this.setState({ isAdd: false });
  }

  loadList = () => {
    this.props.dispatchTurnOnLoading();
    const request = TaskService.getTasks();
    request.then(
      response => {
        this.setState({ todos: response.data });
        this.props.dispatchTurnOffLoading();
      }
    )
    .catch(
      error => {
        console.log(error);
        this.props.dispatchTurnOffLoading();
      }
    );
  }

  render() {
    const { 
      isUpdating, updatingItemId, isEditing, itemEditing, 
      text, itemDeleteId, isSaving, isDeleting, isAdd, isPostingTask,
      isAddNewItem, newItemId
    } = this.state;
    
    return (
      <Page id="tasks">
        <div>
          {
            /* <div>
              <label htmlFor="toggle-edit">Edit
                <input id="toggle-edit" type="checkbox" className={`toggle-edit`} name="isEditing" onChange={() => this.handleChange} checked={this.state.isEditing} />
                <span></span>
              </label>
              <label className="label-toggle-edit" htmlFor="toggle-edit">
                <input type="checkbox" id="toggle-edit" checked={this.state.isEditing} name="isEditing" onChange={this.handleChange} />
                <span className="check-icon">Current Status Edit is: <span className="on-status">On</span><span className="off-status">Off</span></span>
              </label>
            </div> */
          }
          <ul className={ `mb ${isPostingTask ? `is-posting-task` : ''}` }>
          {
            this.state && this.state.todos && this.state.todos.map((item: any, index: number) => (
              <li className={`
                task 
                ${itemDeleteId === item._id ? 'bg-danger' : ''} 
                flex 
                ${(isUpdating && item._id === updatingItemId) ? 'task-processing' : ''} 
                ${item.isDone && `task-done`}
                ${(isAddNewItem && item._id === newItemId) ? 'is-new-item' : ''}
              `} key={index}>
                <div>
                  {
                    (isUpdating &&  item._id === updatingItemId) ?
                      <Loading/> : (
                        (isEditing &&  item._id === itemEditing._id) ?
                        <InProcess/> :
                        <input type="checkbox" className={`mr-1`} checked={item.isDone} name={`isDone-${item._id}`} onChange={(event) => this.updateTodo(item, event)}/>
                      )
                  }
                </div>
                <p className='mr-1'>
                {
                  (isEditing && itemEditing._id === item._id) ?
                  <input type="text" className="input-control" autoFocus value={text} name="text" onChange={(event) => this.handleChange(event)}/> :
                  item.text
                }
                </p>
                {
                  (isEditing && itemEditing._id === item._id) ?
                    <div className="group-btns group-btns-success">
                    <a className='btn center-icon' onClick={() => this.handleCancelTask()}><img src={Resources.cancel} /></a>
                    {
                      isSaving ?
                      <Loading extClasses="negative-pos"/> :
                       <a className='btn center-icon' onClick={() => this.handleSaveTask()}><img src={Resources.save} /></a>
                    }
                    </div> :
                    <div className="group-btns group-btns-primary">
                    <a className='btn center-icon' onClick={() => this.handleEditTask(item)}><img src={Resources.edit} /></a>
                    {
                      (isDeleting && itemDeleteId === item._id) ?
                      <Loading extClasses="negative-pos"/> :
                      <a className='btn center-icon' onClick={() => this.handleDeleteTask(item._id)}><img src={Resources.trash} /></a>
                    }
                    </div>
                }
              </li>
            ))
          }
          </ul>
          
          <a className='btn center-icon btn-success btn-circle mb' onClick={() => this.handleAddTask()}><img src={Resources.add} /></a>
          {
            isAdd && (
              isPostingTask ? (
                <Loading />
              ) : 
              <form action="" method="post" className="panel panel-danger" onSubmit={(event) => this.handleSubmit(event)}>
                <fieldset>
                  <legend className="mb">Add task</legend>
                  <div className="form-control mb-1">
                      <p className="mb">
                        <strong>Is Done</strong>:
                        <span>
                          <input type="checkbox" checked={this.state.isDone} name='isDone' onChange={this.handleChange}/>
                        </span>
                        {text}
                      </p>
                      <p className="mb">
                        <input type="text" className="input-control" placeholder="Input task" value={text} name='text' onChange={this.handleChange}/>
                      </p>
                  </div>
                  <div className="form-control">
                    <input className='btn btn-success btn-style mr' type="submit" value="Save" />
                    <input className='btn btn-default btn-style' onClick={() => this.handleCloseFormAddTask()} type="button" value="Close" />
                  </div>
                </fieldset>
              </form>
            )
          }
        </div>
      </Page>
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    tasks: state.tasks,
    loading: state.loading
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    // dispatchUpdateListTasks: (tasks: any) => {
    //   dispatch(actUpdateListTasks(tasks));
    // },
    dispatchTurnOnLoading: () => (
      dispatch((actTurnOnLoading()))
    ),
    dispatchTurnOffLoading: () => (
      dispatch((actTurnOffLoading()))
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Tasks);
