import * as types from '../../const/types';

const defaultState = false;

const formAddUser = (state = defaultState, action: any) => {
  switch (action.type) {
    case types.TOGGLE_FORM_ADD_USER:
      state = !state;
      return state;
    case types.CLOSE_FORM_ADD_USER:
      state = false;
      return state;
    case types.OPEN_FORM_ADD_USER:
      state = true;
      return state;
    default:
      return defaultState;
  }
}

export default formAddUser;
