import * as dimensionsTypes from '../../const/dimensions';

const defaultState = {
  width: window.innerWidth,
  height: window.innerHeight
};

const dimensions = (state = defaultState, action: any) => {
  switch (action.type) {
    case dimensionsTypes.UPDATE_DIMENSIONS:
      return Object.assign({}, state, action.dimensions);
    default:
      return state;
  }
}

export default dimensions;
