import React, { Component } from 'react';
import { connect } from 'react-redux';
import { filter, includes, orderBy as funcOrderBy } from 'lodash';

import * as resources from '../../resources/index';
import './../UserList/UserList.css';
import User from '../User/User';

class UserList extends Component<any, any> {
  render() {
    let { users } = this.props;
    const { sort } = this.props;
    const { orderBy, orderDir } = sort;

    users = funcOrderBy(users, [orderBy], [orderDir]);

    return (
      <ul className='users'>
        {
          this.getListUserHtml(users)
        }
      </ul>
    )
  }

  // componentWillReceiveProps(nextProps) {
  //   this.updateUserList(nextProps.users);
  // }

  // updateUserList = (lst) => {
  //   this.setState({
  //     users: lst
  //   });

  // }

  // handleOnDelete = id => {
  //   const temp = this.state.users.filter((item) => {
  //     return item.id !== id;
  //   });
  //   this.setState({users: temp});
  // }

  // handleOnEdit = item => {
  //   let temp = this.state.users;
  //   const i = temp.indexOf(item.id);
  //   temp[i] = item;
  //   this.setState({users: temp});
  // }

  getListUserHtml = (users: any) => {
    let x = '<p>empty</p>';
    x = users.map((user: any, index: number) => {
      let src = '', txt = 'no image';
      if (user.src === '' || user.src === undefined) {
        src = resources.noImage;
      } else {
        src = user.src; txt = '';
      }

      return <User user={user} src={src} key={index} txt={txt} />
      // return (
      //   <User user={user} src={src} key={index} txt={txt} />
      //   // pushOnDelete={this.handleOnDelete} pushOnEdit={this.handleOnEdit}
      // );
    });
    return x;
  }
}

const mapStateToProps = (state: any) => {
  return {
    users: state.users,
    sort: state.sort,
  }
};

const mapDispatchToProps = (dispatch: any) => (
  {
    // dispatchUpdate: item => dispatch(actUpdateUser(item)),
  }
);

export default connect(mapStateToProps, mapDispatchToProps)(UserList);
