import * as types from '../const/types';

export const actLoadItems = () => {
  return {
    type: types.GET_ITEMS
  }
}

export const actLoadUser = () => {
  return {
    type: types.LOAD_USER
  }
}

export const actToggleFormAddUser = () => {
  return {
    type: types.TOGGLE_FORM_ADD_USER
  }
}

export const actOpenFormAddUser = () => {
  return {
    type: types.OPEN_FORM_ADD_USER
  }
}

export const actCloseFormAddUser = () => {
  return {
    type: types.CLOSE_FORM_ADD_USER
  }
}

export const actAddUser = (item: any) => {
  return {
    type: types.ADD_USER,
    item
  }
}

export const actDeleteUser = (id: number) => {
  return {
    type: types.DELETE_USER,
    id
  }
}

export const actUpdateUser = (item: any) => {
  return {
    type: types.UPDATE_USER,
    item
  }
}
