import * as types from '../../const/types';
import * as arrHelper from '../../helper/array';

const mockUser = [
  {id: 1, name: 'tot', role: 'husband', old: 30},
  {id: 2, name: 'tuyet', role: 'wife', old: 24},
  {id: 3, name: 'tet', role: 'child', old: 1}
];
const defaultState = mockUser;

const users = (state = defaultState, action: any) => {
  switch (action.type) {
    case types.LOAD_USER:
      return state;
    case types.ADD_USER:
    
      return [
        ...state,
        {
          id: state.length,
          name: action.item.name,
          role: action.item.role,
          old: action.item.old
        }
      ];
    case types.UPDATE_USER: {
      return state.map((item, index) => {
        if (item.id === action.item.id) {
          return Object.assign({}, item, action.item);
        }
        return item;
      });
      // const {item} = action;
      // state = arrHelper.updateObjInArray(state, item);
      // return state;
    }
    case types.DELETE_USER: {
      const {id} = action;
      return arrHelper.removeObjInArrayById(state, id);
    }
    
    default:
      return state;
  }
}

export default users;
