import React, { Component } from 'react';
import './box.scss';

export default class Box extends Component<any, any> {
  render() {
    return (
      <div className={`box ${this.props.fixed ? 'box-fixed' : ''} ${this.props.mask ? 'box-mask' : ''} ${this.props.flexCenterXY ? 'box-flex-center-xy' : ''}`}>
        <div className={`${this.props.childCenter ? 'child-center' : ''}`}>
        {this.props.children}
        </div>
      </div>
    )
  }
}
