import React from 'react';
import './Ads.css';

class Ads extends React.Component<{extClass: string, item: any}> {
  render() {
    let exCls = '';
    const { extClass, item } = this.props;
    if (extClass) {
      exCls = extClass;
    };

    return (
      <div className={`
        ads
        ${item.onMobile ? 'visible_mobile' : ''}
        ${item.onTablet ? 'visible_tablet' : ''}
        ${item.onDesktop ? 'visible_desktop' : ''}
        ${item.onWideDesktop ? 'visible_wide_desktop' : ''}
      `}>
        <img className={`img ${exCls}`} src={item.src} alt={item.alt} />
      </div>
    )
  }
}

export default Ads;
