import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom'
import * as helper from '../../helper/func';
import Header from './Header/Header';
import Footer from './Footer/Footer';
import Content from './Content/Content';
import * as position from '../../const/position';
import { actUpdateCurrentDevices } from '../../actions/devices';
import { actUpdateMainContainerMT } from '../../actions/mainContainerMT';
import { actUpdateDimensions } from '../../actions/dimensions';
import Ads from '../Ads/Ads';
import Container from './Container/Container';
import Alert from './Alert/Alert';
import * as funcTypes from './../../interfaces/funcTypes';
import Loading from './../../component/Loading/Loading';
import BoxFixed from './../../component/boxFixed/boxFixed';
import './Layout.scss';

class Layout extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      topAdsItem: null,
      rightAdsItems: [],
      bottomAdsItem: null,
      leftAdsItems: [],
    };
  }

  updateMainContainerMT = () => {
    if (document.getElementById('main-header')) {
      this.props.dispatchUpdateMainContainerMT({ marginTop: document.getElementById('main-header').clientHeight });
    }
  }

  updateDimensions = () => {
    this.props.dispatchUpdateDimensions({ width: window.innerWidth, height: window.innerHeight });
  }

  componentWillMount() {
    this.updateDimensions();
    if (this.props.ads) {
      // have ads
      this.renderAdsHtml();
    } else {
      // dont have ads
    }
  }

  componentDidMount() {
    window.addEventListener('resize', () => {
      this.updateDimensions();
      this.updateMainContainerMT();
    });
    this.updateMainContainerMT();
  }

  componentWillUnMount() {
    window.removeEventListener('resize', () => {
      this.updateDimensions();
      this.updateMainContainerMT();
    });
  }

  componentWillReceiveProps(nextProps: any) {
  }

  render() {
    const dimensionClass = helper.getDimensionClass();
    const isMobile = helper.isMobile();
    const isTablet = helper.isTablet();
    const isDesktop = helper.isDesktop();
    const isWideDesktop = helper.isWideDesktop();
    const status = this.props.loading.status;

    return (
      <div className={`layout-wrapper ${dimensionClass}`}>
        {
          status &&
          <BoxFixed>
            <Loading/>
          </BoxFixed>
        }
        <Header/>
        <Container extClass='main-content' extStyles={{marginTop: this.props.mainContainerMT.marginTop}}>
          <div className="top-content-layout">
            
            <Alert
              handleClose={this.props.handleClose}
              close={true}
              extClass={'alert-success'}
              title='top content layout' 
              content={''}/>
            <div className="ads-wrap ads-top">
            {
            (
              this.state.topAdsItem && 
              ((isMobile && this.state.topAdsItem.onMobile) ||
              (isTablet && this.state.topAdsItem.onTablet) ||
              (isDesktop && this.state.topAdsItem.onDesktop) ||
              (isWideDesktop && this.state.topAdsItem.onWideDesktop))
            ) ? <Ads item={this.state.topAdsItem} extClass={''} /> : null }
            </div>
          </div>
          <div className="middle-content-layout">
            <Alert
              handleClose={this.props.handleClose}
              close={true}
              extClass={'alert-success'}
              title='middle content layout' 
              content={''}/>
            <Content/>
            <div className="more-content">
              {
                this.props.children
              }
            </div>
          </div>
          <div className="bottom-content-layout">
            <Alert
              handleClose={this.props.handleClose}
              close={true}
              extClass={'alert-success'}
              title='bottom content layout' 
              content={''}/>
          </div>
        </Container>
        <Footer/>
      </div>
    );
  }

  renderAdsHtml = () => {
    this.props.ads.map((item: any, index: number): any => {
      switch (item.position) {
        case position.TOP:
          // Only one ads. Current ads will be replaced by next ads.
          this.setState({ topAdsItem: item });
          break;
        case position.RIGHT:
          // new adsItem will be added to right list adsItem.
          const tmp1 = this.state.rightAdsItems;
          tmp1.push(item);
          this.setState({ rightAdsItems: tmp1 });
          break;
        case position.BOTTOM:
          // Only one adsItem. Current adsItem will be replaced by next adsItem.
          this.setState({ bottomAdsItem: item });
          break;
        case position.LEFT:
          // new adsItem will be added to left list adsItem.
          const tmp2 = this.state.leftAdsItems;
          tmp2.push(item);
          this.setState({ leftAdsItems: tmp2 });
          break;
        default:
          break;
      }
      return null;
    });
  }
}

const mapStateToProps = (state: any) => {
  return {
    ads: state.ads,
    mainContainerMT: state.mainContainerMT,
    loading: state.loading,
  }
}

const mapDispatchToProps = (dispatch: Function) => {
  return {
    dispatchUpdateCurrentDevices: (objDevices: any) => {
      dispatch((actUpdateCurrentDevices(objDevices)));
    },
    dispatchUpdateDimensions: (dimensions: any) => {
      dispatch((actUpdateDimensions(dimensions)));
    },
    dispatchUpdateMainContainerMT: (mTObj: any) => {
      dispatch((actUpdateMainContainerMT(mTObj)));
    },
  }
}

export default withRouter<any>(connect(mapStateToProps, mapDispatchToProps)(Layout));
