import React, { Component } from 'react';
import { firebaseApp } from '../../firebase/firebase';

export default class SignUpForm extends Component<any, any> {
  private emailRef: React.RefObject<HTMLInputElement>;
  private passRef: React.RefObject<HTMLInputElement>;
  private webRef: React.RefObject<HTMLInputElement>;
  
  constructor(props: any) {
    super(props);
    this.emailRef = React.createRef();
    this.passRef = React.createRef();
    this.webRef = React.createRef();
  }

  componentDidMount() {
    this.emailRef.current.focus();
  }

  render() {
  // Use the `ref` callback to store a reference to the text input DOM
  // element in an instance field (for example, this.textInput).
    return (
      <form onSubmit={() => this.handleSignUp(event)} action="" method="post" style={{background: 'orange', padding: '1em'}}>
        <fieldset>
          <legend></legend>
          <div className="form-control mb-1">
            <input className='form-input' type="text" placeholder='Email' name="email" ref={this.emailRef} />
          </div>
          <div className="form-control mb-1">
            <input className='form-input' type="text" placeholder='Password' name="pwd" ref={this.passRef} />
          </div>
          <div className="form-control mb-1">
            <input className='form-input' type="text" placeholder='Website' name="website" ref={this.webRef} />
          </div>
          <div className="form-control">
            <input className='btn btn-primary' type="submit" value="Sign Up" />
          </div>
        </fieldset>
      </form>
    );
  }

  handleSignUp = (event: any) => {
    event.preventDefault();
    console.log(this.emailRef.current.value, this.passRef.current.value, this.webRef.current.value);

    firebaseApp.auth()
    .createUserWithEmailAndPassword(this.emailRef.current.value, this.passRef.current.value)

    .then(data => {
      console.log(data);
    })

    .catch(function(error) {
      // Handle Errors here.
      var errorCode = error.code;
      var errorMessage = error.message;
      // ...
    });
  }
}
