import * as currentUserTypes from '../../const/currentUser';

const defaultState = {
  isLogin: false,
  displayName: '',
  email: '',
  emailVerified: false,
  photoUrl: '',
  isAnonymous: false,
  uid: ''
};

const currentUser = (state = defaultState, action: any) => {
  switch (action.type) {
    case currentUserTypes.SET_USER_LOGIN:
      return Object.assign({}, state, {isLogin: true, ...action.user});
    case currentUserTypes.SET_USER_LOGOUT:
      return Object.assign({}, state, {isLogin: false, ...defaultState});
    default:
      return state;
  }
}

export default currentUser;
