class Container extends Component<any> {
  render () {
    const { children, extClass, extStyles } = this.props;
    return (
      <div className={`container${extClass ? ' ' + extClass : ''}`} style={extStyles?extStyles:null}>
        {children}
      </div>
    )
  }
}

export default Container;
