import * as devicesTypes from '../../const/devices';

const defaultState = {
  mobile: false,
  tablet: false,
  desktop: false,
  wideDesktop: false,
};

const devices = (state = defaultState, action: any) => {
  switch (action.type) {
    case devicesTypes.UPDATE_CURRENT_DEVICE:
      return Object.assign({}, state, action.objDevices);
    default:
      return state;
  }
}

export default devices;
