import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import Container from '../Container/Container';

class Footer extends Component<any> {
  render() {
    let width: any;
    width = 100 / [1,2,3,4].length;
    width = width + '%';

    return (
      <footer className='footer '>
      {/* fixed-bottom fw */}
        <Container>
          <div className="flex justify-content-center">
            {
              [1,2,3,4].map((it, k) => (
                <nav key={k} className='footer-nav' style={{width: width}}>
                  <h3 className='footer-nav-title'>Nav 00{k}</h3>
                  <ul id={`nav-footer-00${it}`}>
                    {
                      this.props.menu.map((item: any, key: number) => (
                        <li key={key}>
                          <NavLink to={item.to} exact={item.exact}>{item.name}</NavLink>
                        </li>
                      )) 
                    }
                  </ul>
                </nav>
              ))
            }
          </div>
        </Container>
      </footer>
    )
  }
}

const mapStateToProps = (state: any) => (
  {
    menu: state.menu
  }
)

export default connect(mapStateToProps, null)(Footer);
