import * as sortTypes from '../../const/sortTypes';

const defaultState = {
  orderBy: 'name',
  orderDir: 'asc'
};

const sort = (state = defaultState, action: any) => {
  const { sort } = action;
  switch (action.type) {
    case sortTypes.SORT_USER:
      let {orderBy, orderDir} = sort;
      let 
        ob = `${orderBy}`.toLowerCase(), 
        od = `${orderDir}`.toLowerCase();
      return {orderBy: ob, orderDir: od};
    default:
      return state;
  }
}

export default sort;
