import React, { Component } from 'react';
import { connect } from 'react-redux';
import { actDeleteUser, actUpdateUser } from '../../actions/index';
import './User.scss';

class User extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      isEdit: false,
      editItem: null,
      name: '',
      role: '',
      old: ''
    };
  }

  componentWillReceiveProps(nextProps: any) {
    // console.log(nextProps);
  }

  render() {
    const { user, src, txt } = this.props;

    return (
      <li className='user mb-2 bs-1 bora-1 flex justify-align-between'>
        <img className='img align-seft-center mr-1' src={src} alt={user.name + txt} />
        <p>
          <span><strong>Id</strong>: 
            {
              user.id
            }
          </span>
        </p>
        <p>
          <span><strong>Name</strong>:
            {
              this.state.isEdit ? <input className='form-input medium' placeholder='name' name='name' value={this.state.name} onChange={this.handleChange} /> : user.name
            }
          </span>
        </p>
        <p>
          <span><strong>Role</strong>: 
          {
            this.state.isEdit ? <input className='form-input short' placeholder='role' name='role' value={this.state.role} onChange={this.handleChange} /> : user.role
          }
          </span>
        </p>
        <p>
          <span><strong>Old</strong>: 
          {
            this.state.isEdit ? <input className="form-input short" placeholder='old' name='old' value={this.state.old} onChange={this.handleChange} /> : user.old
          }
          </span>
        </p>
        <div>
          {
            !this.state.isEdit ?
              <button onClick={() => this.onEdit(user)} className='btn btn-warning mb-1'>Edit</button>
              :
              (
                <div className='group-btn'>
                  <button onClick={() => this.onSave()} className='btn btn-info mb-1'>Save</button>
                  <button onClick={() => this.onCancle()} className='btn btn-danger mb-1'>Cancle</button>
                </div>
              )
          }
          <button className='btn btn-danger' onClick={() => this.onDelete(user.id)}>Delete</button>
        </div>
      </li>
    )
  }

  onDelete = (id: number) => {
    // this.props.pushOnDelete(id);
    this.props.dispatchDelete(id);
  }

  onEdit = (item: any) => {
    this.setState({
      isEdit: true,
      editItem: item,
      name: item.name,
      role: item.role,
      old: item.old
    });
  }

  handleChange = (e: any) => {
    const name = e.target.name;
    const value = e.target.value;
    this.setState({
      [name]: value
    });
  }
  
  onSave = () => {
    // this.props.pushOnEdit(item);
    if(this.state.editItem !== null) {
      const { name, role, old } = this.state;
      const tmp = {
        id: this.state.editItem.id,
        name, role, old
      };
      this.setState({ editItem: tmp });
      this.props.dispatchUpdate(tmp);
    }
    this.setState({
      isEdit: false,
      editItem: null,
      name: '',
      role: '',
      old: ''
    });
  }

  onCancle = () => {
    this.setState({
      isEdit: false,
      editItem: null,
      name: '',
      role: '',
      old: ''
    });
  }
}

// const mapStateToProps = state => {
//   return {
//     users: state.users
//   }
// };

const mapDispatchToProps = (dispatch: any) => {
  return {
    dispatchDelete: (id: number) => dispatch(actDeleteUser(id)),
    dispatchUpdate: (item: any) => {
      dispatch(actUpdateUser(item));
    }
  }
};

export default connect(null, mapDispatchToProps)(User);
