import React, { Component } from 'react';
import * as funcTypes from './../../interfaces/funcTypes';
import './Dropdown.scss';

class Dropdown extends Component<{show: boolean, list: any, handleClick: funcTypes.FunctionType1}> {
  render() {
    return (
      <ul className={`list dropdown dropdown-primary ${this.props.show ? 'show' : ''}`}>
        {
          this.props.list.map((item: any, index: number) => (
            <li key={index} className='dropdown-item' onClick={() => this.props.handleClick(item)}>{`${item.orderBy} - ${item.orderDir}`}</li>
          ))
        }
      </ul>
    )
  }
}

export default Dropdown;
