import React from 'react';

class WindowDimensions extends React.Component<any, any> {
  render () {
    const { width, height } = this.props.dimensions;
    return (
      <p style={{ color: 'white' }}>
        <span>
          {width} x {height}
        </span>
      </p>
    )
  }
}

export default WindowDimensions;
