import React, { Component } from 'react';
import './Alert.css';
import * as funcTypes from './../../../interfaces/funcTypes';

class Alert extends Component<any> {
  render () {
    const {handleClose, close, title, content, extClass, extStyles} = this.props;
    return (
      <div 
        className={`alert ${close ? 'alert-close' : ''} ${extClass ? extClass : ''}`} 
        style={extStyles?extStyles:null}
      >
        <h3 className='alert-title'>{title}</h3>
        <p className='alert-content'>{content}</p>
        {
          close ? 
          <button onClick={handleClose} className='btn alert-close-btn'>x</button>
          : null
        }
      </div>
    )
  }
}

export default Alert;
