import React, { Component } from 'react';
import { connect } from 'react-redux';
import { actSortUser } from './../../actions/sort';
import './Sort.css';
import WrapDropdown from '../WrapDropdown/WrapDropdown';

class Sort extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      wrapDropdown: {
        color: 'primary',
        extClass: 'sort-list',
        btnName: 'Sort',
      }
    };
  }

  componentWillReceiveProps(nextProps: any) {
    this.setState({ listSortUser: nextProps.listSortUser });
  }

  render() {
    const { color, extClass, btnName } = this.state.wrapDropdown;
    return (
      <div className='sort'>
        <WrapDropdown 
          list={this.props.listSortUser} 
          handleClick={this.handleClick}
          color={color}
          extClass={extClass}
          btnName={btnName}
        />
      </div>
    )
  }

  handleClick = (sort: any) => {
    // let {b,d} = sort;
    // b = this.toLower(b);
    // d = this.toLower(d);
    // const sortLower = {
    //   orderBy: b,
    //   orderDir: d
    // };
    // console.log(sortLower);
    this.props.dispatchSortUser(sort);
  }
}

const mapStateToProps = (state: any) => {
  return {
    listSortUser: state.listSortUser,
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    dispatchSortUser: (sort: any) => {
      dispatch(actSortUser(sort))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Sort);
