import React, { Component } from 'react';
import { connect } from 'react-redux';
import { actAddUser, actCloseFormAddUser } from '../../actions/index';
import * as resources from '../../resources/index';
import * as funcTypes from './../../interfaces/funcTypes';
import './FormAddUser.css';

class FormAddUser extends Component<
{status: boolean, dispatchSubmitFormAddUser: funcTypes.FunctionType1, dispatchCloseFormAddUser: funcTypes.FunctionType2}, 
{name: string, role: string, old: number, nameValid: boolean, roleValid: boolean, oldValid: boolean}> {
  constructor(props: any) {
    super(props);
    this.state = {
      name: '',
      role: '',
      old: -1,
      nameValid: false,
      roleValid: false,
      oldValid: false
    };
  }

  componentWillReceiveProps(nextProps: any) {
  }

  render() {
    return (
      <div className={`wrap-modal ${this.props.status ? 'show' : ''}`}>
        <div className="mask-blur"></div>
        <div className="modal modal-sm">
          <div className="modal-heading">
            <h3 className="modal-title">Add User</h3>
          </div>
          <div className="modal-body">
            <div className="row justify-content-center">
              <div className="col w-30-per">
                <div className="block-avatar">
                  <img className='img' src={resources.noImage} alt={'no'} />
                  <button>Choose avatar</button>
                </div>
              </div>
              <div className="col w-70-per">
                <div className="block-input flex flex-column">
                  <input type="text" className={`form-input ${this.state.nameValid ? 'input-text-invalid' : ''}`} placeholder="Name" name="name" onKeyUp={(e) => {this.handleKeyUp(e)}} onChange={(e) => this.handleChange(e)} />
                  <input type="text" className={`form-input ${this.state.roleValid ? 'input-text-invalid' : ''}`} placeholder="Role" name="role" onKeyUp={(e) => {this.handleKeyUp(e)}} onChange={(e) => this.handleChange(e)} />
                  <input type="text" className={`form-input ${this.state.oldValid ? 'input-text-invalid' : ''}`} placeholder="Old" name="old" onKeyUp={(e) => {this.handleKeyUp(e)}} onChange={(e) => this.handleChange(e)} />
                </div>
              </div>
            </div>
          </div>
          <div className="modal-footer">
            <div className="group-btn flex justify-content-center align-items-center">
              <button className='btn' onClick={() => {this.handleSubmit()}}>Submit</button>
              <button className='btn' onClick={() => {this.handleCancle()}}>Cancle</button>
            </div>
          </div>
        </div>
      </div>
    )
  }

  handleKeyUp = (e: any) => {
    const nameValid = this.state.name === '' && true;
    const roleValid = this.state.role === '' && true;
    const oldValid = this.state.old === -1 && true;
    const validInput = nameValid && roleValid && oldValid;
    if (e.key === 'enter' && validInput) {
      console.log('ok')
    } else {
      this.setState({
        nameValid,
        roleValid,
        oldValid
      });
    }
  }

  handleChange = (e: any) => {
    const tname = e.target.name;
    const value = e.target.value;
    switch (tname) {
      case 'name':
        this.setState({
          name: value
        });
        break;
      case 'role':
        this.setState({
          role: value
        });
        break;
      case 'old':
        this.setState({
          old: value
        });
        break;
      default:
        break;
    }
    
  }
  
  handleSubmit = () => {
    const item = {
      name: this.state.name,
      role: this.state.role,
      old: this.state.old
    };
    this.props.dispatchSubmitFormAddUser(item);
  }

  handleCancle = () => {
    this.props.dispatchCloseFormAddUser();
    this.setState({
      name: '',
      role: '',
      old: -1
    });
  }
}

const mapStateToProps = (state:any) => {
  return {
    formAddUser: state.formAddUser
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    dispatchAdd: (item: any) => {
      dispatch(actAddUser(item));
    },
    dispatchCloseFormAddUser: () => {
      dispatch(actCloseFormAddUser());
    },
    dispatchSubmitFormAddUser: (user: any) => {
      dispatch(actAddUser(user));
      dispatch(actCloseFormAddUser());
    }
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(FormAddUser);
