import React from 'react';
import Home from './pages/Home/Home';
import Main from './pages/Main/Main';
import About from './pages/About/About';
import User from './pages/User/User';
import Product from './pages/Product/Product';
import Map from './pages/Map/Map';
import SignUp from './pages/SignUp/SignUp';
import SignIn from './pages/SignIn/SignIn';
import Tasks from './pages/Tasks/Tasks';
import NotFound from './component/Layout/404/404';
const routes = [
  {
    path: '/',
    exact: true,
    main: (match: any, location: any) => <Home match={match} location={location} />
  },
  {
    path: '/tasks',
    exact: true,
    main: () => <Tasks/>
  },
  {
    path: '/about',
    exact: true,
    main: () => <About/>
  },
  {
    path: '/signup',
    exact: true,
    main: () => <SignUp/>
  },
  {
    path: '/signin',
    exact: true,
    main: () => <SignIn/>
  },
  {
    path: '/main',
    exact: true,
    main: (location: any) => <Main location={location}/>
  },
  {
    path: '/user',
    exact: true,
    main: () => <User/>
  },
  {
    path: '/product',
    exact: true,
    main: () => <Product/>
  },
  {
    path: '/map',
    exact: true,
    main: () => <Map/>
  },
  {
    path: '',
    exact: true,
    main: () => <NotFound/>
  },
];

export default routes;
