import * as devices from '../const/devices';

export const findIndexById = (arr: [], id: number) => {
  for (let i = 0; i < arr.length; i++) {
    const ele: any = arr[i];
    if(ele.id===id)
      return i;
  }
  return -1;
}

export const findItemById = (arr: [], id: number) => {
  for (let i = 0; i < arr.length; i++) {
    const ele: any = arr[i];
    if(ele.id===id)
      return ele;
  }
  return null;
}

export const getDimensionClass = () => {
  const w = window.innerWidth;
  const md = 768, lg = 992, xl = 1200;
  let rs = devices.IS_MOBILE.toLowerCase();

  if (w >= md)
    rs = devices.IS_TABLET.toLowerCase();
  if (w >= lg)
    rs =  devices.IS_DESKTOP.toLowerCase();
  if (w >= xl)
    rs =  devices.IS_WIDE_DESKTOP.toLowerCase();

  return rs;
}

export const isMobile = () => {
  return getDimensionClass() === devices.IS_MOBILE.toLowerCase();
}
export const isTablet = () => {
  return getDimensionClass() === devices.IS_TABLET.toLowerCase();
}
export const isDesktop = () => {
  return getDimensionClass() === devices.IS_DESKTOP.toLowerCase();
}
export const isWideDesktop = () => {
  return getDimensionClass() === devices.IS_WIDE_DESKTOP.toLowerCase();
}
